@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body{margin:0;font-family:'Quicksand', sans-serif;font-size:2rem;color:#89929b;display:flex;justify-content:center;align-items:center;height:100vh;width:100vw;background:#181818}h1{font-family:'Bebas Neue', cursive;font-size:4rem;color:#89929b;-webkit-animation:blink 1s ease-in-out 0.5s;animation:blink 1s ease-in-out 0.5s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}.board-row:after{clear:both;content:'';display:table}.status{margin-bottom:10px}.square{background:#21262d;border:1px solid #161b22;float:left;font-family:'Rubik Mono One', sans-serif;font-size:3rem;font-weight:bold;line-height:34px;color:#0a0a0a;height:60px;width:60px;margin-right:-1px;margin-top:-1px;padding:0;text-align:center;transition:all 0.15s ease-in-out}.square:hover{background:#2c323b}.square-winning{background:#65a765;-webkit-transform:scale(1.1) rotate(360deg);transform:scale(1.1) rotate(360deg)}.square-winning:nth-of-type(1){transition-delay:0}.square-winning:nth-of-type(2){transition-delay:0.2s}.square-winning:nth-of-type(3){transition-delay:0.4s}.square:focus{outline:none}.kbd-navigation .square:focus{background:#ddd}.game{padding:2rem;width:60vw;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:70vh;display:flex;flex-direction:row;align-items:center;justify-content:space-around;background:#161b22;border:1px solid #21262d;border-radius:1rem;box-shadow:0 0 1rem #0a0a0a}.game-board{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:flex;justify-content:center;align-items:center}.game-info{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content;display:flex;flex-direction:column;align-items:center;justify-content:center}.game-info ul{list-style-type:none;-webkit-padding-start:0;padding-inline-start:0;-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0}.game-info ul li{margin:0.5rem auto}.game-info ul li button{font-family:'Quicksand', sans-serif;font-weight:500;font-size:1.2rem;color:#89929b;border-radius:1.1rem;border:0;padding:0.6rem;margin:auto;text-align:center;width:18rem;background:#21262d}.winner{transition:0.3s all ease;color:#65a765;-webkit-animation:blink 1s ease-in-out 0.5s;animation:blink 1s ease-in-out 0.5s;-webkit-animation-iteration-count:3;animation-iteration-count:3}@-webkit-keyframes blink{0%{opacity:100%}50%{opacity:0%}100%{opacity:100%}}@keyframes blink{0%{opacity:100%}50%{opacity:0%}100%{opacity:100%}}@media screen and (max-width: 900px){html{font-size:80%}body{height:-webkit-max-content;height:max-content;min-height:100vh;padding:1rem 0}.game{flex-direction:column;height:auto;width:auto}}

